@import url(https://fonts.googleapis.com/css2?family=Glory:wght@300&display=swap);
/*IMPORTS*/

html {
  font-family: 'Glory', sans-serif;
  background-image: url('https://img.purch.com/rc/1920x1200/aHR0cDovL3d3dy5zcGFjZS5jb20vaW1hZ2VzL2kvMDAwLzAxNS8yMjMvb3JpZ2luYWwvY2FyaW5hLW5lYnVsYS0xOTIwLTEyMDAuanBn');
}

/*centering and sizing my box for quotes*/
.quote-box {
  margin-top: 20vh;
  max-width: 35vw;
  margin-right: auto;
  margin-left: auto;

  padding: 1.5rem;
  background-color: white;
  border-radius: 0.625rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  letter-spacing: 1px;
}

.quote-mark {
  font-size: 26px;
}

.quote-text {
  font-size: 2rem;
  font-weight: 500;
}

.author {
  margin-top: 0.625rem;
}

.tweet-container {
  margin-left: auto;
}

/*Position of Rocket*/
.newQuote {
  position: absolute;
  bottom: 25vh;
  margin-left: 47vw;
  margin-right: auto;
}

.rocket {
  font-size: 5rem; /*adjusting size of rocket icon*/
  color: black;
}

/* effects when you hover over rocket */
.rocket:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  color: #551a8b;
  opacity: 0.5;
  transition: opacity 0.1s ease-out;
  -moz-transition: opacity 0.1s ease-out;
  -webkit-transition: opacity 0.1s ease-out;
  -o-transition: opacity 0.1s ease-out;
}

/* size and position of loading spinner */
.lds-spinner {
  left: 47%;
  margin-top: 25vh;
  -webkit-transform: scale(2);
          transform: scale(2);
}

 /* Adjusting size for mobile devices */
@media only screen and (max-width: 720px) {
  .quote-text {
    font-size: 20px;
  }
  .newQuote {
    bottom: 10vh;
  }
}

/*Settings for pure css loader to work*/

.lds-spinner {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-spinner div {
  -webkit-transform-origin: 40px 40px;
          transform-origin: 40px 40px;
  -webkit-animation: lds-spinner 1.2s linear infinite;
          animation: lds-spinner 1.2s linear infinite;
}
.lds-spinner div:after {
  content: ' ';
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
  width: 6px;
  height: 18px;
  border-radius: 20%;
  background: #fcf;
}
.lds-spinner div:nth-child(1) {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-animation-delay: -1.1s;
          animation-delay: -1.1s;
}
.lds-spinner div:nth-child(2) {
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}
.lds-spinner div:nth-child(3) {
  -webkit-transform: rotate(60deg);
          transform: rotate(60deg);
  -webkit-animation-delay: -0.9s;
          animation-delay: -0.9s;
}
.lds-spinner div:nth-child(4) {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-animation-delay: -0.8s;
          animation-delay: -0.8s;
}
.lds-spinner div:nth-child(5) {
  -webkit-transform: rotate(120deg);
          transform: rotate(120deg);
  -webkit-animation-delay: -0.7s;
          animation-delay: -0.7s;
}
.lds-spinner div:nth-child(6) {
  -webkit-transform: rotate(150deg);
          transform: rotate(150deg);
  -webkit-animation-delay: -0.6s;
          animation-delay: -0.6s;
}
.lds-spinner div:nth-child(7) {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
.lds-spinner div:nth-child(8) {
  -webkit-transform: rotate(210deg);
          transform: rotate(210deg);
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
}
.lds-spinner div:nth-child(9) {
  -webkit-transform: rotate(240deg);
          transform: rotate(240deg);
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.lds-spinner div:nth-child(10) {
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
  -webkit-animation-delay: -0.2s;
          animation-delay: -0.2s;
}
.lds-spinner div:nth-child(11) {
  -webkit-transform: rotate(300deg);
          transform: rotate(300deg);
  -webkit-animation-delay: -0.1s;
          animation-delay: -0.1s;
}
.lds-spinner div:nth-child(12) {
  -webkit-transform: rotate(330deg);
          transform: rotate(330deg);
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
@-webkit-keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

